import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CartService } from '../cart.service';
import {UserLoginService} from './../user-login.service';
import { Women} from './../women';
@Component({
  selector: 'app-women',
  templateUrl: './women.component.html',
  styleUrls: ['./women.component.css']
})
export class WomenComponent implements OnInit {
  women:Women=[];
  private items:Object=[];
  categories = [
    {category: 'Women Tshirt'},
    {category: 'Women Shoes'},
   
  
  ];
  private category: any;
 
  private productList:Object=[];
  constructor(private router:Router,private service:UserLoginService,private cartService:CartService)
   {

    }
  public sendCategory(cat:String)
  {
    let response=this.service.getAllItems(cat);
    response.subscribe((data)=>this.women=data);
  }
  public addToCart(product)
  {
    let response=this.cartService.doAdd(product);
    response.subscribe((data)=>this.items=data);
    window.alert('Your product has been added to the cart!');
  }
  
  ngOnInit() {
  }

}
