import { Injectable } from '@angular/core';
import { Cart} from './cart';
import {HttpClient} from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class CartService {

  constructor(private http:HttpClient) {}
  items = [];

  public doAdd(cart: Cart) {
    return this.http.post("http://localhost:8080/addToCart", cart, {responseType: 'text' as 'json'});
  }
  gettingCartItems()
    {
        return this.http.get("http://localhost:8080/getcartitems");
    } 
    // public clearCart()
    // {
    //   return this.http.delete("http://localhost8080/clearcart");
    // }
    deleteItem(id)
    {
      return this.http.delete("http://localhost:8080/rem/"+id);
    }
 
}